@import "node_modules/node-normalize-scss/_normalize";
@import "icons/_iconfont__variables";
@import "icons/_iconfont__icons";
@import url('https://fonts.googleapis.com/css?family=Bitter:400,700|Open+Sans:300,300i,400,600,700');

html{
    font-size: 10px;
    font-family: "OpenSans", sans-serif;
}
body{
    font-size: 16px;
    max-width: 1980px;
    margin: auto;
    backface-visibility: hidden;
}
.media-text {
    font-size: 2rem;
    font-family: "OpenSans", sans-serif;
    color: #fff;
    line-height: 1;
    letter-spacing: 1rem;
    font-weight: bold;
    text-shadow: 5px 2px 2px black;
}
.link {
    white-space: nowrap;
    font-size: 2.6rem;
    font-family: "OpenSans", sans-serif;
    color: rgb(255, 255, 255);
    line-height: 1.575;
    text-decoration: none;
    
    &:hover{
        color: black;
    }
    
    &--nav{
        font-size: 15px;
        font-family: "OpenSans", sans-serif;
        color: rgb(255, 255, 255);
        font-weight: bold;
        line-height: 1.6;
        
        &:not(:last-child){
            margin-right: 2rem;
        }
    }
    &--project{
        color: silver;
    }
    &--contact{
        color: #ed767a;
        line-height: 1;
    }
}
.h1{
    font-size: 6rem;
    font-family: "Bitter", serif;
    color: rgb(255, 255, 255);
    line-height: 1.2;
    font-weight: normal;
}
.h2{
    font-size: 4.3rem;
    font-family: "Bitter", serif;
    color: rgb(73, 73, 73);
    font-weight: bold;
    line-height: 0.558;
    position: absolute;
    left: 20px;
    top: 0;
}
.secondary-text{
    font-size: 1.6rem;
    font-family: "OpenSans", sans-serif;
    color: rgb(190, 190, 190);
    line-height: 1.846;
}
.description-text{
    font-size: 1.6rem;
    font-family: "OpenSans", sans-serif;
    color: black;
    line-height: 1.714;
    text-align: left;
    max-width: 70%;
}
ul{
    padding: 0;
}
li{
    list-style: none;
}
.button{
    cursor: pointer;
    
    &--contact{
        padding: 2rem 4rem;
        font-size: 2rem;
        font-family: "OpenSans", sans-serif;
        color: rgb(255, 255, 255);
        line-height: 1.2;
        text-align: center;
        background-color: #fd4c46;
        border: 2px solid #fff;
        border-radius: 5px;
        transition: 300ms;
        text-transform: uppercase;
        
        &:hover{
            background-color: #ed767a;
            transform: scale(1.2);
            box-shadow: 20px 20px 10px rgba(0,0,0,0.5);
        }
        
        &:active{
            background-color: darkred;
        }
    }
    
    &--close{
        position: absolute;
        background: none;
        border: none;
        padding: 1rem;
        top: 15px;
        right: 15px;
        
        &:before, &:after{
            content: " ";
            position: absolute;
            width: 2rem;
            height: 2px;
            background-color: #646464;
            left: 0;
        }
        &:before{
            transform: rotatez(45deg);
        }
        &:after{
            transform: rotateZ(-45deg)
        }
        
    }
}
.header{
    position: absolute;
    top: 100px;
    left: 0;
    width: 100%;
    background-color: #007EA7;
    z-index: 9;
    
    &__content-wrapper{
        max-width: 1980px;
        margin: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1rem 2rem;
    }
    
    &__logo{
        
        .rounder{
            width: 94px;
            height: 94px;
            background-color: none;
            border-radius: 50px;
            position: relative;
            box-shadow: inset 20px 20px #e6e443;
        }
        
        .logo-text{
            color: #fff;
            position: absolute;
            left: 30%;
            top: 50%;
            transform: translatey(-50%);
        }
    }
}
.section{
    text-align: center;
    position: relative;
    padding: 8rem 4rem;
    
    &--introduction{
        background: linear-gradient(to top, rgba(58,58,58,0.5), rgba(58,58,58,0.5)), url(../../images/Background.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        background-attachment: fixed;
    }
    
    &--about-me{
        
        &__content-wrapper{
            display: flex;
            justify-content: space-around;
            align-items: center;
            padding: 4rem 0;
            
            .img-wrapper{
                width: 20rem;
                height: 26.7rem;
                margin-right: 2rem;
                
                img{
                    width: 20rem;
                    height: 26.7rem;
                    border-radius: 15px;
                }
            }
        }
    }
    
    &--skills{
        
        &__content-wrapper{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            overflow: hidden;
            
            .skill{
                width: 25%;
                padding: 10rem 0 5rem 0;
                font-size: 1.8rem;
                line-height: 1.2;
                font-weight: bold;
                text-transform: uppercase;
                position: relative;
                user-select: none;
                transition: 300ms;
                
                
                &:nth-child(2):before{
                    content: " ";
                    position: absolute;
                    background-image: url(../../images/sass.svg);
                    background-repeat: no-repeat;
                    height: 50px;
                    width: 50px;
                    transform: translatex(50%);
                    top: 25%;
                }
                &:nth-child(3):before{
                    content: " ";
                    position: absolute;
                    background-image: url(../../images/javascript.svg);
                    background-repeat: no-repeat;
                    height: 50px;
                    width: 50px;
                    transform: translatex(50%);
                    top: 25%;
                }
                &:nth-child(4):before{
                    content: " ";
                    position: absolute;
                    background-image: url(../../images/jquery.png);
                    background-repeat: no-repeat;
                    background-size: contain;
                    height: 50px;
                    width: 50px;
                    transform: translatex(20%);
                    top: 25%;
                }
                &:nth-child(5):before{
                    content: "B";
                    position: absolute;
                    font-size: 4rem;
                    background-color: rebeccapurple;
                    color: #fff;
                    border-radius: 5px;
                    height: 50px;
                    width: 50px;
                    transform: translatex(50%);
                    top: 25%;
                }
                &:nth-child(6):before{
                    content: " ";
                    position: absolute;
                    background-image: url(../../images/foundation.png);
                    background-size: contain;
                    background-repeat: no-repeat;
                    height: 50px;
                    width: 50px;
                    transform: translatex(80%);
                    top: 25%;
                }
                &:nth-child(7):before{
                    content: " ";
                    position: absolute;
                    background-image: url(../../images/gulp.png);
                    background-size: contain;
                    background-repeat: no-repeat;
                    height: 50px;
                    width: 50px;
                    transform: translatex(50%);
                    top: 25%;
                }
                &:last-child:before{
                    content: " ";
                    position: absolute;
                    background-image: url(../../images/git.png);
                    background-size: contain;
                    background-repeat: no-repeat;
                    height: 50px;
                    width: 50px;
                    transform: translatex(20%);
                    top: 25%;
                }
                &:hover{
                    transform: scale(2);
                }
            }
            .double-icons{
                &:first-child:before{
                    content: " ";
                    position: absolute;
                    right: 50%;
                    top: 25%;
                    height: 50px;
                    width: 50px;
                    background-image: url(../../images/html.svg);
                }
                &:first-child:after{
                    content: " ";
                    position: absolute;
                    background-size: contain;
                    left: 50%;
                    top: 25%;
                    height: 50px;
                    width: 50px;
                    background-image: url(../../images/css.svg);
                }
            }
        }
    }
    
    &--portfolio{
        
        &__content-wrapper{
            display: flex;
            flex-wrap: wrap;
            
            .work-example-wrapper{
                transition: 300ms;
                width: 33%;
                margin-bottom: 5rem;
                padding: 2rem 0;
                backface-visibility: hidden;
                
                .work-description{
                    font-size: 2rem;
                    font-family: "OpenSans", sans-serif;
                    line-height: 2.174;
                    max-width: 30rem;
                    margin: auto;
                }
                &:hover{
                    transform: translate(0, -1rem) scale(1.05);
                    box-shadow: 0px 3px 4px rgba(0,0,0,0.4);
                    border-radius: 20px;
                }
            }
        }
    }
}
.footer{
    background-image: url(../../images/bg_footer.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    padding: 14rem 0;
    margin-bottom: 10rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    .h2{
        position: static;
        color: #fff;
        text-transform: uppercase;
    }
}
.pop-up{
    position: fixed;
    width: 100%;
    max-width: 60rem;
    height: 30rem;
    display: none;
    background-color: #fff;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0 auto;
    padding: 0 2rem;
    border-radius: 25px;
    border: 2px solid black;
    box-shadow: 20px 20px 20px black;
    
    &__content-wrapper{
        display: flex;
        flex-direction: column;
        width: 100%;
        
        .contact-wrapper{
            display: flex;
            margin-bottom: 2rem;
        }
        .contact-type{
            font-size: 2.6rem;
            font-family: "OpenSans", cursive;
            color: #00bd9b;
            line-height: 1;
        }
    }
    .h2{
        position: static;
    }
}